.intro {
  display: flex;
  height: 100vh;
}

.intro-left {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.intro-right {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.intro-left-wrapper {
  width: 50%;
}

.intro-start {
  font-size: 2.5rem;
  font-weight: 300;
}

.intro-name {
  font-size: 3rem;
  font-weight: 600;
}

.intro-title {
  height: 50px;
  font-size: 2rem;
  font-weight: bold;
  color: #59b256;
  height: 100%;
  font-weight: 300;
}

.intro-image {
  height: 70%;
  margin-left: 10%;
}

.intro-description {
  font-size: 1.5rem;
  font-weight: 300;
}

.floating {
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes floating {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 25px);
  }
  100% {
    transform: translate(0, -0px);
  }
}
